import { Button, Form, Input, Modal, Select, Table } from 'antd';
import confirm from 'antd/lib/modal/confirm';
import employeeAPi from 'api/auth-api';
import EmployeeAPi from 'api/employee-api';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import { Toast } from 'components/Common';
import { Loading } from 'components/Common/Loading';
import { Employee } from 'models';
import React, { useEffect, useState } from 'react';
import { employeeActions } from '../employeeSlice';

export const ListEmployeePage = () => {
  const dispatch = useAppDispatch();
  const listEmployee = useAppSelector((state) => state.employee.list);
  const isLoading = useAppSelector((state) => state.employee.loading);
  const [selectedEmployee, setselectedEmployee] = useState<Employee>();
  useEffect(() => {
    dispatch(employeeActions.fetchEmployeeList());
  }, [dispatch]);

  //Delete Employee
  const confirmDelete = async () =>{
    try {
      if (selectedEmployee?._id) await employeeAPi.remove(selectedEmployee._id);
      Toast(
        'success',
        'Xo nhn vin thnh cng!',
        'nhn vin c xo thnh cng. Bn c th xem li trong danh sch nhn vin.'
      );
      setisShowDetailModal(false)
      dispatch(employeeActions.fetchEmployeeList());
    } catch (error: any) {
      Toast('danger', 'Xo nhn vin tht bi!', error.response.data.error);
    }
  }
  const handleDelete = async () => {

    confirm({
      title: 'Bn c chc chn mun xo nhn vin ny khng?',
      // icon: <ExclamationCircleOutlined />,
      content: 'Nhn vin mt khi  b xo, s khng th hon tc...',
      okText: 'Xo',
      okType: 'danger',
      cancelText: 'Hu',
      onOk() {
        confirmDelete()
      },
      onCancel() {
        console.log('Cancel');
      },
    });
    
  };

  //Add table
  const [isShowAddModal, setisShowAddModal] = useState<boolean>(false);

  const handleCancelAddModal = () => {
    setisShowAddModal(false);
  };

  const onFormSubmitAddModal = async (employee: Employee) => {
    try {
      await EmployeeAPi.add(employee);
      Toast(
        'success',
        'Thm nhn vin thnh cng!',
        'nhn vin mi c to thnh cng. Bn c th xem li trong danh sch nhn vin.'
      );
      dispatch(employeeActions.fetchEmployeeList());
      setisShowAddModal(false);
    } catch (error: any) {
      Toast('danger', 'Thm nhn vin tht bi!', error.response.data.error);
    }

    console.log(employee);
  };
  const footerOfAddModal = [
    <Button key="back" onClick={() => handleCancelAddModal()}>
      Thot
    </Button>,

    <Button
      form="AddForm"
      icon={<i className="fas fa-save"></i>}
      type="primary"
      key="submit"
      htmlType="submit"
    >
      &nbsp;Thm
    </Button>,
  ];

  // -- Detail customer Modal
  const [isShowDetailModal, setisShowDetailModal] = useState(false);
  const handleShowDetailModal = async (obj: Employee) => {
    console.log(obj);

    await setselectedEmployee(obj);
    await setisShowDetailModal(true);
  };
  const handleCancelDetailModal = () => {
    setisShowDetailModal(false);
  };

  const onFormSubmitDetailModal = async (employee: Employee) => {
    try {
      if (employee._id) await EmployeeAPi.update(employee._id, employee);
      Toast(
        'success',
        'Cp nht nhn vin thnh cng!',
        'nhn vin c cp nht thnh cng. Bn c th xem li trong danh sch nhn vin.'
      );
      dispatch(employeeActions.fetchEmployeeList());
      setisShowDetailModal(false);
    } catch (error: any) {
      Toast('danger', 'Cp nht nhn vin tht bi!', error.response.data.error);
    }
  };
  const footerOfDetailModal = [
    <Button key="back" onClick={() => handleCancelDetailModal()}>
      Thot
    </Button>,

    <Button
      form="DetailForm"
      icon={<i className="fas fa-save"></i>}
      type="primary"
      key="submit"
      htmlType="submit"
    >
      &nbsp;Cp nht
    </Button>,
  ];

  const columns: any = [
    {
      title: 'Tn nhn vin',
      dataIndex: 'name',
      key: 'name',
      render: (text: string) => <strong> {text} </strong>,
    },

    {
      title: 'Email',
      dataIndex: 'email',
      key: 'email',
      // width: '20%'
    },
    {
      title: 'S in thoi',
      dataIndex: 'phone',
      key: 'phone',
      // width: '20%'
    },
    {
      title: 'a ch',
      dataIndex: 'address',
      key: 'address',
      // width: '20%'
    },

    {
      title: 'Chi tit',
      key: 'lastOnline',
      render: (obj: Employee) => {
        return (
          <div>
            {' '}
            <Button
              icon={<i className="fas fa-user-circle"></i>}
              type="primary"
              onClick={() => handleShowDetailModal(obj)}
            >
              &nbsp;&nbsp;Chi tit
            </Button>
          </div>
        );
      },
    },
  ];
  return (
    <div>
      {isLoading === true ? (
        <Loading />
      ) : (
        <div>
          <div className="text-end">
            <Button onClick={() => setisShowAddModal(true)} type="primary">
              Nhn vin mi
            </Button>
          </div>

          <Modal
            closable={false}
            style={{ top: 20 }}
            title={<strong>Thm nhn vin:</strong>}
            visible={isShowAddModal}
            footer={footerOfAddModal}
          >
            <div>
              <Form
                id="AddForm"
                labelCol={{ span: 7 }}
                wrapperCol={{ span: 20 }}
                layout="horizontal"
                onFinish={onFormSubmitAddModal}
              >
                <Form.Item
                  label="Tn :"
                  name="name"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="Email:"
                  name="email"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="Mt khu:"
                  name="password"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input.Password />
                </Form.Item>
                <Form.Item
                  label="a ch: "
                  name="address"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="S in thoi:"
                  name="phone"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input />
                </Form.Item>
                <Form.Item label="Select" name="role">
                  <Select>
                    <Select.Option value="employee">Nhn vin</Select.Option>
                    <Select.Option value="manager">Qun l</Select.Option>
                    <Select.Option value="admin">Qun tr vin</Select.Option>
                  </Select>
                </Form.Item>
              </Form>
            </div>
          </Modal>
          {isShowDetailModal && (
            <Modal
              closable={false}
              style={{ top: 20 }}
              title={<strong>Chi tit khch hng:</strong>}
              visible={isShowDetailModal}
              footer={footerOfDetailModal}
            >
              <div>
                <Form
                  id="DetailForm"
                  labelCol={{ span: 7 }}
                  wrapperCol={{ span: 20 }}
                  layout="horizontal"
                  onFinish={onFormSubmitDetailModal}
                  initialValues={selectedEmployee}
                >
                  <Form.Item
                    label="M nhn vin :"
                    name="_id"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input disabled />
                  </Form.Item>
                  <Form.Item
                    label="Tn :"
                    name="name"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input />
                  </Form.Item>
                  <Form.Item
                    label="Email:"
                    name="email"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input />
                  </Form.Item>

                  <Form.Item
                    label="a ch: "
                    name="address"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input />
                  </Form.Item>
                  <Form.Item
                    label="S in thoi:"
                    name="phone"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input />
                  </Form.Item>
                </Form>
              </div>
              <div className="text-center">
                <Button 
                onClick={()=>handleDelete()}
                icon={<i className="fas fa-trash"></i>} type="primary" danger>
                  &nbsp; Xo nhn vin ny
                </Button>
              </div>
            </Modal>
          )}
          <Table
            rowKey="_id"
            columns={columns}
            dataSource={listEmployee}
            pagination={false}
            scroll={{ y: 800 }}
          />
        </div>
      )}
    </div>
  );
};
