import { Button, Form, Image, Input, InputNumber, Modal, Select, Table, Tag } from 'antd';
import confirm from 'antd/lib/modal/confirm';
import productApi from 'api/product-api';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import { Toast } from 'components/Common';
import { categoryActions } from 'features/category/categorySlice';
import { Category, FilterFormat, Product, ProductResponse } from 'models';
import React, { useEffect, useState } from 'react';
import { productActions } from '../productSlice';

export const ProductPage = () => {
  const dispatch = useAppDispatch();
  const listProduct: Product[] = useAppSelector((state) => state.product.list);
  const listCategory: Category[] = useAppSelector((state) => state.category.list);
  const listCategoryFormatted: FilterFormat[] = [];
  const [selectedProduct, setselectedProduct] = useState<ProductResponse>();
  listCategory.forEach((category) => {
    let filerItem: FilterFormat = { text: category.name, value: category.name };
    listCategoryFormatted.push(filerItem);
  });

  // handle Delete Product
  const confirmDelete = async () => {
    try {
      if (selectedProduct?._id) await productApi.remove(selectedProduct._id);
      Toast(
        'success',
        'Xo sn phm thnh cng!',
        'sn phm c xo thnh cng. Bn c th xem li trong danh sch sn phm.'
      );
      setisShowDetailModal(false);
      dispatch(productActions.fetchProductList());
    } catch (error: any) {
      Toast('danger', 'Xo sn phm tht bi!', error.response.data.error);
    }
  };
  const handleDelete = async () => {
    confirm({
      title: 'Bn c chc chn mun xo sn phm ny khng?',
      // icon: <ExclamationCircleOutlined />,
      content: 'sn phm mt khi  b xo, s khng th hon tc...',
      okText: 'Xo',
      okType: 'danger',
      cancelText: 'Hu',
      onOk() {
        confirmDelete();
      },
      onCancel() {
        console.log('Cancel');
      },
    });
  };

  // -- Add Product Modal
  const [isShowAddModal, setisShowAddModal] = useState(false);

  const onFormSubmitAddModal = async (product: ProductResponse) => {
    product.images = [];
    console.log(product);
    try {
      await productApi.add(product);
      Toast(
        'success',
        'Thm sn phm thnh cng!',
        'sn phm mi c to thnh cng. Bn c th xem li trong danh sch sn phm.'
      );
      dispatch(productActions.fetchProductList());
      setisShowAddModal(false);
    } catch (error: any) {
      Toast('danger', 'Thm sn phm tht bi!', error.response.data.error);
    }
  };
  const footerOfAddModal = [
    <Button key="back" onClick={() => setisShowAddModal(false)}>
      Thot
    </Button>,

    <Button
      form="AddForm"
      icon={<i className="fas fa-save"></i>}
      type="primary"
      key="submit"
      htmlType="submit"
    >
      &nbsp;Thm
    </Button>,
  ];
  // -- Detail Product Modal
  const [isShowDetailModal, setisShowDetailModal] = useState(false);
  const handleShowDetailModal = async (product: Product) => {
    console.log(product.category._id);

    let productById = await productApi.getById(product._id);
    await setselectedProduct(productById);
    await setisShowDetailModal(true);
  };
  const handleCancelDetailModal = () => {
    setisShowDetailModal(false);
  };

  const onFormSubmitDetailModal = async (product: any) => {
    try {
      if (product._id) await productApi.update(product._id, product);
      Toast(
        'success',
        'Cp nht sn phm thnh cng!',
        'sn phm c cp nht thnh cng. Bn c th xem li trong danh sch sn phm.'
      );
      dispatch(productActions.fetchProductList());
      setisShowDetailModal(false);
    } catch (error: any) {
      Toast('danger', 'Cp nht sn phm tht bi!', error.response.data.error);
    }
  };
  const footerOfDetailModal = [
    <Button key="back" onClick={() => handleCancelDetailModal()}>
      Thot
    </Button>,

    <Button
      form="DetailForm"
      icon={<i className="fas fa-save"></i>}
      type="primary"
      key="submit"
      htmlType="submit"
    >
      &nbsp;Cp nht
    </Button>,
  ];

  useEffect(() => {
    dispatch(productActions.fetchProductList());
    dispatch(categoryActions.fetchCategoryList());
  }, [dispatch]);
  const columns: any = [
    {
      title: 'Tn sn phm',
      dataIndex: 'name',
      key: 'name',
      render: (text: string, obj: any) => (
        <div className="row">
          <Image width={60} src={obj.images[0]} />
          <strong className="col-6"> {text} </strong>
        </div>
      ),
      width: '25%',
    },
    {
      title: 'Gi bn',
      dataIndex: 'price',
      key: 'price',
      render: (price: number) =>
        new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'VND' }).format(price),
      sorter: {
        compare: (a: Product, b: Product) => a.price - b.price,
        multiple: 3,
      },
    },
    {
      title: 'Loi',
      dataIndex: ['category', 'name'],
      key: 'category',
      filters: listCategoryFormatted,
      filterMode: 'tree',
      filterSearch: true,
      onFilter: (value: string, record: Product) => record?.category.name.includes(value),
    },
    {
      title: 'description',
      dataIndex: 'description',
      key: 'description',
    },
    {
      title: 'Trng thi',
      dataIndex: 'isFeatured',
      key: 'isFeatured',
      render: (tag: boolean) => {
        if (tag === false) return <Tag color="red">{(tag + '').toUpperCase()}</Tag>;
        else return <Tag color="green">{(tag + '').toUpperCase()}</Tag>;
      },
    },
    {
      title: 'Ngy to',
      dataIndex: 'dateCreated',
      key: 'dateCreated',
    },
    {
      title: 'Chi tit',
      key: 'lastOnline',
      render: (obj: Product) => {
        return (
          <div>
            {' '}
            <Button
              icon={<i className="fas fa-user-circle"></i>}
              type="primary"
              onClick={() => handleShowDetailModal(obj)}
            >
              &nbsp;&nbsp;Chi tit
            </Button>
          </div>
        );
      },
    },
  ];
  return (
    <div>
      <div className="text-end">
        <Button
          onClick={() => setisShowAddModal(true)}
          type="primary"
          // icon={ <UserOutlined />}
        >
          Sn phm mi
        </Button>
      </div>
      <Table
        rowKey="_id"
        columns={columns}
        dataSource={listProduct}
        pagination={false}
        scroll={{ y: 800 }}
      />

      <Modal
        closable={false}
        style={{ top: 20 }}
        title={<strong>Thm sn phm:</strong>}
        visible={isShowAddModal}
        footer={footerOfAddModal}
      >
        <div>
          <Form
            id="AddForm"
            labelCol={{ span: 7 }}
            wrapperCol={{ span: 20 }}
            layout="horizontal"
            onFinish={onFormSubmitAddModal}
          >
            {/* <Form.Item
              label="Hnh nh :"
              name="images"
              rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
              hasFeedback
            >
              <Input disabled />
            </Form.Item> */}
            <Form.Item
              label="Tn sn phm :"
              name="name"
              rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
              hasFeedback
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="Gi:"
              name="price"
              rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
              hasFeedback
            >
              <InputNumber />
            </Form.Item>

            <Form.Item
              label="Loi"
              name="category"
              rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
            >
              <Select>
                {listCategory.map((category, index) => {
                  return <Select.Option value={category._id}>{category.name}</Select.Option>;
                })}
              </Select>
            </Form.Item>
            {/* <Form.Item label="Trng thi" name="isFeatured" rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}>
              {' '}
              <Select>
                <Select.Option value="false">M BN</Select.Option>
                <Select.Option value="true">HT HNG</Select.Option>
              </Select>
            </Form.Item> */}
            <Form.Item name="description" label="M t">
              <Input.TextArea />
            </Form.Item>
          </Form>
        </div>
      </Modal>
      {isShowDetailModal && (
        <Modal
          closable={false}
          style={{ top: 20 }}
          title={<strong>Chi tit sn phm:</strong>}
          visible={isShowDetailModal}
          footer={footerOfDetailModal}
        >
          <div>
            <Form
              id="DetailForm"
              labelCol={{ span: 7 }}
              wrapperCol={{ span: 20 }}
              layout="horizontal"
              onFinish={onFormSubmitDetailModal}
              initialValues={selectedProduct}
            >
              <Form.Item
                label="M sn phm :"
                name="_id"
                rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                hasFeedback
              >
                <Input disabled />
              </Form.Item>
              <Form.Item
                label="Tn sn phm :"
                name="name"
                rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                hasFeedback
              >
                <Input />
              </Form.Item>
              <Form.Item
                label="Gi:"
                name="price"
                rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                hasFeedback
              >
                <InputNumber />
              </Form.Item>

              <Form.Item label="Loi" name="category">
                <Select>
                  {listCategory.map((category, index) => {
                    return <Select.Option value={category._id}>{category.name}</Select.Option>;
                  })}
                </Select>
              </Form.Item>
              <Form.Item label="trng thi" name="isFeatured">
                {' '}
                <Select defaultValue="false">
                  <Select.Option value="false">M BN</Select.Option>
                  <Select.Option value="true">HT HNG</Select.Option>
                </Select>
              </Form.Item>
              <Form.Item name="description" label="M t">
                <Input.TextArea />
              </Form.Item>
            </Form>
            <div className="text-center">
              <Button
                onClick={() => handleDelete()}
                icon={<i className="fas fa-trash"></i>}
                type="primary"
                danger
              >
                &nbsp; Xo sn phm ny
              </Button>
            </div>
          </div>
        </Modal>
      )}
    </div>
  );
};
