import { OrderedListOutlined, UserOutlined } from '@ant-design/icons';
import { Button, Form, Input, Modal, Pagination, Table, Tag } from 'antd';
import Search from 'antd/lib/input/Search';
import confirm from 'antd/lib/modal/confirm';
import CustomerAPi from 'api/customer-api';
import { useAppDispatch, useAppSelector } from 'app/hooks';
import { Toast } from 'components/Common';
import { Loading } from 'components/Common/Loading';
import { OrderActions } from 'features/order/orderSlice';
import { Customer, ListParams, Product } from 'models';
import React, { useEffect, useState } from 'react';
import { customerActions } from '../customerSlice';


let locale = {
  emptyText: 'Khng tm thy kt qu no',
};
export const ListCustomerPage = () => {
  const dispatch = useAppDispatch();
  const listCustomer = useAppSelector((state) => state.customer.list);
  const isLoading = useAppSelector((state) => state.customer.loading);
  const listOrderByCustomer = useAppSelector((state) => state.order.rawData.data);
  const totalPage = useAppSelector((state) => state.order.rawData.totalPage);
  const isListOrderLoading = useAppSelector((state) => state.order.loading);
  const [selectedCustomer, setselectedCustomer] = useState<Customer>();
  const [currentPage, setcurrentPage] = useState<number>(1);

  useEffect(() => {
    dispatch(customerActions.fetchCustomerList());
  }, [dispatch]);

  // handle Delete customer
  const confirmDelete = async () =>{
    try {
      if (selectedCustomer?._id) await CustomerAPi.remove(selectedCustomer._id);
      Toast(
        'success',
        'Xo khch hng thnh cng!',
        'Khch hng c xo thnh cng. Bn c th xem li trong danh sch khch hng.'
      );
      setisShowDetailModal(false)
      dispatch(customerActions.fetchCustomerList());
    } catch (error: any) {
      Toast('danger', 'Xo khch hng tht bi!', error.response.data.error);
    }
  }
  const handleDelete = async () => {

    confirm({
      title: 'Bn c chc chn mun xo khch hng ny khng?',
      // icon: <ExclamationCircleOutlined />,
      content: 'Khch hng mt khi  b xo, s khng th hon tc...',
      okText: 'Xo',
      okType: 'danger',
      cancelText: 'Hu',
      onOk() {
        confirmDelete()
      },
      onCancel() {
        console.log('Cancel');
      },
    });
    
  };
  // handle Search 
  const onSearch = (query: string) => {
    if (query === '') {
      dispatch(customerActions.fetchCustomerList());
    } else dispatch(customerActions.fetchCustomerResultListWhenSearch(query));
  };

  const handleCustomerOrderClick = async (obj: Customer | undefined) => {
    setcurrentPage(1)
    setselectedCustomer(obj)
    let params: ListParams = {
      id: obj?._id,
      page: 1,
    };
    
    await dispatch(OrderActions.fetchOrderListByCustomer(params));
    await setisShowListOrderModal(true);
  };
  // -- Add customer Modal
  const [isShowAddModal, setisShowAddModal] = useState(false);

  const onFormSubmitAddModal = async (customer: Customer) => {
    try {
      await CustomerAPi.add(customer);
      Toast(
        'success',
        'Thm khch hng thnh cng!',
        'Khch hng mi c to thnh cng. Bn c th xem li trong danh sch khch hng.'
      );
      dispatch(customerActions.fetchCustomerList());
      setisShowAddModal(false);
    } catch (error: any) {
      Toast('danger', 'Thm khch hng tht bi!', error.response.data.error);
    }
  };
  const footerOfAddModal = [
    <Button key="back" onClick={() => setisShowAddModal(false)}>
      Thot
    </Button>,

    <Button
      form="AddForm"
      icon={<i className="fas fa-save"></i>}
      type="primary"
      key="submit"
      htmlType="submit"
    >
      &nbsp;Thm
    </Button>,
  ];
  // -- Detail customer Modal
  const [isShowDetailModal, setisShowDetailModal] = useState(false);
  const handleShowDetailModal = async (obj: Customer) => {
    

    await setselectedCustomer(obj);
    await setisShowDetailModal(true);
  };
  const handleCancelDetailModal = () => {
    setisShowDetailModal(false);
  };

  const onFormSubmitDetailModal = async (customer: Product) => {
    try {
      if (customer._id) await CustomerAPi.update(customer._id, customer);
      Toast(
        'success',
        'Cp nht khch hng thnh cng!',
        'Khch hng c cp nht thnh cng. Bn c th xem li trong danh sch khch hng.'
      );
      dispatch(customerActions.fetchCustomerList());
      setisShowDetailModal(false);
    } catch (error: any) {
      Toast('danger', 'Cp nht khch hng tht bi!', error.response.data.error);
    }
  };
  const footerOfDetailModal = [
    <Button key="back" onClick={() => handleCancelDetailModal()}>
      Thot
    </Button>,

    <Button
      form="DetailForm"
      icon={<i className="fas fa-save"></i>}
      type="primary"
      key="submit"
      htmlType="submit"
    >
      &nbsp;Cp nht
    </Button>,
  ];

  const columns: any = [
    {
      title: 'Tn khch hng',
      dataIndex: 'name',
      key: 'name',
      render: (text: string) => <strong>{text}</strong>,
    },

    {
      title: 'Email',
      dataIndex: 'email',
      key: 'email',
    },
    {
      title: 'S in thoi',
      dataIndex: 'phone',
      key: 'phone',
    },
    {
      title: 'a ch',
      dataIndex: 'address',
      key: 'address',
    },

    {
      title: 'Chi tit',
      key: 'lastOnline',
      width: '25%',
      render: (obj: Customer) => {
        return (
          <div>
            {' '}
            <Button
              onClick={() => handleShowDetailModal(obj)}
              icon={<i className="fas fa-user-circle"></i>}
              type="primary"
            >
              &nbsp;&nbsp;Chi tit
            </Button>
            &nbsp;&nbsp;
            <Button
              onClick={() => handleCustomerOrderClick(obj)}
              danger
              icon={<OrderedListOutlined />}
              type="primary"
            >
              &nbsp;&nbsp;Lch s mua hng
            </Button>
          </div>
        );
      },
    },
  ];
  // List Orders Customer
  const [isShowListOrderModal, setisShowListOrderModal] = useState(false);
  const handleCancleListOrderModal = () => {
    setisShowListOrderModal(false);
  };
  const handlePageListOrderOnChange = async (page: number) =>{
    
    setcurrentPage(page)
    let params: ListParams = {
      id: selectedCustomer?._id,
      page: page,
    };
    
    await dispatch(OrderActions.fetchOrderListByCustomer(params));
  }
  const footerOfListOrderlModal = [
    <Button key="back" onClick={() => handleCancleListOrderModal()}>
      Thot
    </Button>,
  ];
  const ListOrderTableColumns: any = [
    {
      title: 'M ho n',
      dataIndex: '_id',
      key: '_id',
      render: (text: string) => <strong>{text}</strong>,
      width: '20%',
    },
    {
      title: 'Ngi lp H',
      dataIndex: ['employee', 'name'],
      key: 'name',
      render: (text: string) => <strong>{text}</strong>,
    },

    {
      title: 'Tng tin',
      dataIndex: 'totalPrice',
      key: 'totalPrice',
      render: (price: number) =>
        new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'VND' }).format(price),
    },

    {
      title: 'Trng thi',
      dataIndex: 'status',
      key: 'status',
      render: (tag: string) => {
        if (tag === 'Pending') return <Tag color="#2db7f5">{(tag + '').toUpperCase()}</Tag>;
        else return <Tag color="#87d068">{(tag + '').toUpperCase()}</Tag>;
      },
    },
    {
      title: 'Ngy to',
      dataIndex: 'dateOrdered',
      key: 'dateOrdered',
    },
    // {
    //   title: 'Chi tit',
    //   key: 'lastOnline',
    //   render: (obj: OrderResponse) => {
    //     return (
    //       <div>
    //         {' '}
    //         <Button
    //           icon={<i className="fas fa-user-circle"></i>}
    //           type="primary"
    //           // onClick={() => showModal(obj)}
    //         >
    //           &nbsp;&nbsp;Chi tit
    //         </Button>
    //       </div>
    //     );
    //   },
    // },
  ];
  return (
    <div>
      {isLoading === true ? (
        <Loading />
      ) : (
        <div>
          <div className="row">
            &nbsp;{' '}
            <div className="col-5 text-start">
              <Search
                placeholder="Nhp thng tin ..."
                onSearch={(query) => onSearch(query)}
                style={{ width: 200 }}
              />
              <Button  type="primary" onClick={() => dispatch(customerActions.fetchCustomerList())}>
                Mc nh
              </Button>
            </div>
            <div className="col-6 text-end">
              <Button onClick={() => setisShowAddModal(true)} type="primary"
              icon={ <UserOutlined />}
              >
                Khch hng mi
              </Button>
            </div>
          </div>

          <Table
            rowKey="_id"
            columns={columns}
            dataSource={listCustomer}
            pagination={false}
            scroll={{ y: 800 }}
            locale={locale}
          />
          <Modal
            closable={false}
            style={{ top: 20 }}
            title={<strong>Thm khch hng:</strong>}
            visible={isShowAddModal}
            footer={footerOfAddModal}
          >
            <div>
              <Form
                id="AddForm"
                labelCol={{ span: 7 }}
                wrapperCol={{ span: 20 }}
                layout="horizontal"
                onFinish={onFormSubmitAddModal}
              >
                <Form.Item
                  label="Tn :"
                  name="name"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input />
                </Form.Item>

                <Form.Item
                  label="Email:"
                  name="email"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="Mt khu:"
                  name="password"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input.Password />
                </Form.Item>
                <Form.Item
                  label="a ch: "
                  name="address"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input />
                </Form.Item>
                <Form.Item
                  label="S in thoi:"
                  name="phone"
                  rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                  hasFeedback
                >
                  <Input />
                </Form.Item>
              </Form>
            </div>
          </Modal>
          {isShowDetailModal && (
            <Modal
              closable={false}
              style={{ top: 20 }}
              title={<strong>Chi tit khch hng:</strong>}
              visible={isShowDetailModal}
              footer={footerOfDetailModal}
            >
              <div>
                <Form
                  id="DetailForm"
                  labelCol={{ span: 7 }}
                  wrapperCol={{ span: 20 }}
                  layout="horizontal"
                  onFinish={onFormSubmitDetailModal}
                  initialValues={selectedCustomer}
                >
                  <Form.Item
                    label="M khch hng :"
                    name="_id"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input disabled />
                  </Form.Item>
                  <Form.Item
                    label="Tn :"
                    name="name"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input />
                  </Form.Item>
                  <Form.Item
                    label="Email:"
                    name="email"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input />
                  </Form.Item>

                  <Form.Item
                    label="a ch: "
                    name="address"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input />
                  </Form.Item>
                  <Form.Item
                    label="S in thoi:"
                    name="phone"
                    rules={[{ required: true, message: 'Thuc tnh ny l bt buc!' }]}
                    hasFeedback
                  >
                    <Input />
                  </Form.Item>
                </Form>
                <div className="text-center">
                <Button 
                onClick={()=>handleDelete()}
                icon={<i className="fas fa-trash"></i>} type="primary" danger>
                  &nbsp; Xo khch hng ny
                </Button>
              </div>
              </div>
            </Modal>
          )}
          <div>
            <Modal
              width={1000}
              title="Basic Modal"
              visible={isShowListOrderModal}
              footer={footerOfListOrderlModal}
              closable={false}
            >
              {isListOrderLoading === false ? 
              <div>

                <Table
                  rowKey="_id"
                  columns={ListOrderTableColumns}
                  dataSource={listOrderByCustomer}
                  pagination={false}
                  scroll={{ y: 800 }}
                />
                <div className="text-center">
                  <Pagination
                    showSizeChanger={false}
                    current={currentPage}
                    onChange={(e) => handlePageListOrderOnChange(e)}
                    total={totalPage * 10}
                  />
                </div>
              </div> : <Loading/> }
            </Modal>
          </div>
        </div>
      )}
    </div>
  );
};
